@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro&display=swap);
.Preloader_preLoaderWrapper__26hOR{position:absolute;width:100%;height:100vh;top:0;left:0}.Preloader_preLoaderWrapper__26hOR img{position:absolute;top:50%;bottom:0;right:0;left:50%;transform:translate(-50%, -50%)}

